#projects{
  overflow:hidden;
  padding-top: 5rem;
  background-color: cadetblue;
  box-sizing: border-box;
}

#project-screen{
  width: 95%;
  height: 90%;
  border-radius: 50px;
  margin: auto;
  margin-top: 0.8rem;
  background-color: #c2e1fb;
  border: 1.5rem solid black;
}

#project-pane{
  position: relative;
  height: 50px;
  width: 100%;
  border-radius: 25px 25px 0 0;
}

.project-card{
  border: 1px solid black;
  float: left;
  margin: 0.5rem;
  position: relative;
}

.project-card-description{
  padding: 0.5rem;
  background-color: black;
  color: white;
  height: 15rem;
  background-color: rgba(0,0,0,0.7);
  display: none;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 0;
}

#project-content-list {
  width: 95%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: 1rem auto;
}

#project-content {
  width: 100%;
  height: 90%;
}

.project-image{
  height: 15rem;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-color: black;
}